



















import { Component, Vue } from 'vue-property-decorator';
import TopUsers from '@/components/top/TopUsers.vue';

@Component({
  components: {
    TopUsers,
  },
})

export default class ProfitList extends Vue {
  private tabActive?: number = 0;
  private isLoading?: boolean = false;
  private loading?: boolean = false;
  private finished?: boolean = false;
  private tabList?: any[] = [
    '日榜',
    '昨日榜',
    '周榜',
    '月榜',
  ];
  private changeTab(index: number): void {
    this.tabActive = index;
  }
  private onLoad(): void {
    setTimeout(() => {
      this.loading = false;
      this.finished = true;
    }, 1000);
  }
  private onRefresh () {
    setTimeout(() => {
      this.isLoading = false;
    }, 1000);
  }
}
