























import { Component, Vue } from 'vue-property-decorator';
import ProfitList from '@/components/top/ProfitList.vue';
import ContributionList from '@/components/top/ProfitList.vue';

@Component({
  components: {
    ProfitList,
    ContributionList,
  },
})

export default class Top extends Vue {
  private tabActive?: number = 0;
  private tabList?: any[] = [
    '收益榜',
    '贡献榜',
  ];
  private changeTab(index: number): void {
    this.tabActive = index;
  }
  private goBackPage() {
    this.$router.back();
  }
}
